import './App.css';
import './odometer-theme-car.css';
import React, { useState } from 'react';
import Button from '@mui/material/Button';
import { Typography } from '@mui/material';
import Odometer from 'react-odometerjs';


const _MS_PER_DAY = 1000 * 60 * 60 * 24;

function getAsDays(a) {
  return Math.floor(Date.UTC(a.getFullYear(), a.getMonth(), a.getDate()) / _MS_PER_DAY);
}

// a and b are javascript Date objects
function dateDiffInDays(a, b) {
  // Discard the time and time-zone information.
  const utc1 = Date.UTC(a.getFullYear(), a.getMonth(), a.getDate());
  const utc2 = Date.UTC(b.getFullYear(), b.getMonth(), b.getDate());

  return Math.floor((utc2 - utc1) / _MS_PER_DAY);
}
function roundToNearestHundred(val)
{
  return Math.round((val)/100)*100;
}

function App() {
  const [startValue] = useState(5800);
  const [startDate] = useState(new Date(2021, 10, 8));

  const currentDate = new Date();

  const differenceInDays = dateDiffInDays(startDate, currentDate);
  const totalLeaseKM = 30000;
  const totalLeaseDays = 365*2;
  const targetForToday = differenceInDays/totalLeaseDays*totalLeaseKM; 

  const [currentValue, setCurrentValue] = useState(roundToNearestHundred(startValue + targetForToday));


  const currentlyDriven = Math.floor(currentValue-startValue);

  // <Slider aria-label="Lease Start Kilometers" defaultValue={defaultStartValue} step={100} min={defaultStartValue} max={30000} 
  // onChange={(event, newValue) => setStartValue(newValue)} />

  return (
    <div className="App">
        <p>
          <Typography>Current Mileage</Typography>
          <Odometer value={currentValue} format='d' animation='count' duration={250} theme='car' />
        </p>
        <p>
          <Button variant="contained" onClick={(event) => setCurrentValue(currentValue - 100)}>-</Button>
          {' '}
          <Button variant="contained" onClick={(event) => setCurrentValue(currentValue + 100)}>+</Button>
        </p>
        <p>
          <Typography variant='h5'>You are {Math.floor(targetForToday - currentlyDriven)} km from target</Typography>
        </p>
        <p>
          <Typography>You have been driving for {differenceInDays} days </Typography>
          <Typography>You have driven {currentlyDriven} km </Typography>
          <Typography>Your target is {Math.floor(targetForToday)} km</Typography>
          <Typography>Your extrapolated total is {Math.floor(currentlyDriven*totalLeaseDays / differenceInDays)} km</Typography>
          <Typography>Lease Start Kilometers: {startValue}</Typography>
        </p>
    </div>
  );
}

export default App;
